import Vue from '@/components/base'
import { Component } from 'vue-property-decorator'
import template from './NoticeView.vue'
import { sleep } from '@yzfe/utils'

@Component({
    name: 'NoticeView',
    mixins: [template]
})
export default class NoticeView extends Vue {
    //#region datas ******************************/
    //#endregion datas ***************************/

    //#region props ******************************/
    //#endregion props ***************************/

    //#region vuex *******************************/
    //#endregion vuex ****************************/

    //#region computed ***************************/
    //#endregion computed ************************/

    //#region watchers ***************************/
    //#endregion watchers ************************/

    //#region life cycle *************************/
    protected created() {}

    protected async mounted() {
        let duration = 5000

        this.$confirm('这是一段内容这是一段内容。', {
            type: 'info',
            title: '确定？'
        })

        this.$notify({
            title: '普通提示',
            type: 'info',
            duration,
            message: '这是一条普通的信息反馈。',
            showClose: true,
            customClass: 'fff'
        })

        await sleep(500)

        this.$notify({
            title: '成功提示',
            type: 'success',
            duration,
            message: '这是一条成功的信息反馈。',
            showClose: true
        })

        await sleep(500)

        this.$notify({
            title: '警告提示',
            type: 'warning',
            duration,
            message: '这是一条警告的信息反馈。',
            showClose: true
        })

        await sleep(500)

        this.$notify({
            title: '错误提示',
            type: 'error',
            duration,
            message: '这是一条错误的信息反馈。',
            showClose: true
        })

        this.$message({
            message: '这是一条普通的信息反馈。',
            duration,
            showClose: true
        })

        await sleep(500)

        this.$message({
            type: 'success',
            message: '这是一条普通的信息反馈。',
            duration,
            showClose: true
        })

        await sleep(500)

        this.$message({
            type: 'warning',
            message: '这是一条普通的信息反馈。',
            duration,
            showClose: true
        })

        await sleep(500)

        this.$message({
            type: 'error',
            message: '这是一条普通的信息反馈。',
            duration,
            showClose: true
        })

        await sleep(500)
    }
    //#endregion life cycle **********************/

    //#region hooks ******************************/
    //#endregion hooks ***************************/

    //#region event handler **********************/
    //#endregion event handler *******************/

    //#region methods ****************************/
    //#endregion methods *************************/
}
